export default function generateResourceImpl(option = {}) {
  const useExternal = option.external === undefined ? true : !!option.external;

  return function resourceModifyImpl({ js, externals, css }, kylinApp, program) {
    if ( useExternal ) {
      // external模式
      if (program.prod) {
        js['Vue'] = 'https://gw.alipayobjects.com/as/g/h5-lib/vue/2.5.13/vue.min.js';
      } else {
        js['Vue'] = 'https://gw.alipayobjects.com/as/g/h5-lib/vue/2.5.13/vue.js';
      }
    } else {
      // alias模式
      delete externals['vue'];
      delete js['Vue'];
      delete css['Vue'];
    }
    

    return;
  };
}
